// 导航栏组件
import cuCustom from '@/colorui/components/cu-custom.vue';
export default {
	components: {
		cuCustom
	},
	data() {
		return {
			// 控制自定义导航栏是否字体加粗
			isBold: true,
			// 评论
			value:'',
			// 订单id
			id:2
		};
	},
	methods:{
		publish(){
			uni.request({
				url: 'http://47.104.29.236:9999/api/v1/comment',
				method: 'POST',
				data: { id: this.id,value:this.value},
				success: res => {
					uni.navigateTo({
						url: '../order/order',
						success: res => {},
						fail: () => {},
						complete: () => {}
					});
				},
				fail: () => {},
				complete: () => {}
			});
		}
	},
	onLoad(e){
		this.id = e.id
	}
};
